import React from "react";
import Nav from "../../components/Navbar/Nav";
import { useState, useEffect } from "react";
import axios from "axios";
import { toast } from "react-hot-toast";
import { Checkbox, Radio } from "antd";
import { Prices } from "../../components/Prices/Price.js";
import { useNavigate } from "react-router-dom";
import { useCart } from "../../Context/cart";
import { FaRupeeSign } from "react-icons/fa";
import "./product.css";
import SearchInput from "../../components/Forms/SearchInput.js";

const AllProducts = () => {
  const [products, Setproducts] = useState([]);
  const [categories, SetCategories] = useState([]);
  const [checked, SetChecked] = useState([]);
  const [radio, SetRadio] = useState([]);
  const [total, SetTotal] = useState(0);
  const [page, SetPage] = useState(1);
  const [loading, SetLoading] = useState(false);
  const Navigate = useNavigate();
  const [cart, SetCart] = useCart();
  // getTotal product
  const GetTotal = async () => {
    try {
      const { data } = await axios.get("/api/admin/product/product-count");
      SetTotal(data?.totalcount);
    } catch (error) {
      toast.error("cannot get toatl");
    }
  };

  useEffect(() => {
    const LoadMore = async () => {
      try {
        const { data } = await axios.get(
          `/api/admin/product/product-list/${page}`
        );
        SetLoading(false);
        Setproducts([...products, ...data?.products]);
      } catch (error) {
        SetLoading(false);
      }
    };
    if (page === 1) return;
    LoadMore();
  }, [page]);

  //handle Filter
  const HandleFilter = async (value, id) => {
    let all = [...checked];
    if (value) {
      all.push(id);
    } else {
      all = all.filter((c) => c !== id);
    }
    SetChecked(all);
  };
  useEffect(() => {
    //get filtered products
    const FilterProduct = async () => {
      try {
        const { data } = await axios.post("/api/admin/product/filter-product", {
          checked,
          radio,
        });

        Setproducts(data?.products);
        // toast.success(data.message);
      } catch (error) {
        toast.error("something went wrong");
      }
    };
    if (checked.length || radio.length) {
      FilterProduct();
    }
  }, [checked, radio]);

  const getCategories = async () => {
    try {
      const { data } = await axios.get("/api/admin/category/get-category");
      if (data?.success) {
        SetCategories(data?.categories);
      }
    } catch (error) {
      toast.error("cannot get categories");
    }
  };
  useEffect(() => {
    getCategories();
    GetTotal();
  }, []);

  useEffect(() => {
    const GetAllProducts = async () => {
      try {
        SetLoading(true);
        const { data } = await axios.get(
          `/api/admin/product/product-list/${page}`
          // `/api/admin/product/get-allproducts`
        );
        SetLoading(false);

        if (data?.success) {
          Setproducts(data?.products);
          toast.success(data.message);
        } else {
          toast.error(data.message);
        }
      } catch (error) {
        SetLoading(false);

        toast.error("something went wrong");
      }
    };

    GetAllProducts();
  }, []);

  return (
    <>
      <Nav />
      <div className="products_main  ">
        <div className="d-flex  container-fluid">
          <div className="col-md-2 col-sm-12 mt-3 ">
            {" "}
            <div className="categoryFilter_section">
              <h6 className="text-center text-uppercase  font-monospace  fw-bold ">
                filter by
                <span className="text-muted  fw-bold bg-warning ms-2 p-1 rounded-3">
                  {" "}
                  Category
                </span>{" "}
              </h6>{" "}
              <div
                className="p-1 py-3  mt-2 rounded-3"
                style={{
                  boxShadow:
                    "rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px",
                }}
              >
                {categories.map((c) => (
                  <>
                    <div className="d-flex  flex-wrap ms-4   " key={c._id}>
                      <Checkbox
                        onChange={(e) => {
                          HandleFilter(e.target.checked, c._id);
                        }}
                        className="fw-bold"
                      >
                        {c.name}
                      </Checkbox>
                    </div>
                  </>
                ))}
              </div>
            </div>
            <div className="priceFilter_section mt-4 m-2">
              <h6 className="text-center text-uppercase  font-monospace  fw-bold mb-2">
                filter by{" "}
                <span className="text-muted  fw-bold  bg-warning ms-1  p-1 rounded-3">
                  {" "}
                  price
                </span>{" "}
              </h6>{" "}
              <div
                className="py-3 rounded-3"
                style={{
                  boxShadow:
                    "rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px",
                }}
              >
                <Radio.Group onChange={(e) => SetRadio(e.target.value)}>
                  {Prices?.map((p) => (
                    <>
                      <div className="d-flex row flex-wrap ms-4  " key={p._id}>
                        <Radio className="fw-bold" value={p.array}>
                          {p.name}
                        </Radio>
                      </div>
                    </>
                  ))}
                </Radio.Group>{" "}
              </div>
            </div>
            <div className=" d-flex flex-column mt-3">
              <button
                className="btn btn-success py-3 fw-bold mx-3  "
                onClick={() => {
                  window.location.reload();
                }}
              >
                RESET FILTERS
              </button>
            </div>
          </div>

          <div className="col-md-10  col-sm-12  ps-4 ">
            {window.location.pathname == "/allproducts" && (
              <div className="px-5">
                <div className=" pe-5  ">
                  {" "}
                  <SearchInput />
                </div>{" "}
              </div>
            )}
            <div className="d-flex flex-wrap ">
              {products?.map((product) => (
                <>
                  <div
                    className="card m-3 "
                    style={{ height: "20rem", width: "15rem" }}
                  >
                    <img
                      src={`/api/admin/product/product-photo/${product._id}`}
                      className="card-img-top cardimg"
                      alt={product.productName}
                    />
                    <div className="card-body">
                      <h5 className="card-title text-capitalize  text-center fw-bold">
                        {product.productName}
                      </h5>
                      <hr />

                      <p className="card-text text-capitalize">
                        {" "}
                        {product.description.substring(0, 30)}
                      </p>

                      <h5 className="card-text fw-bold">
                        <FaRupeeSign className="h6 mt-1" /> {product.price}.00/-
                      </h5>
                      <div className="d-flex  flex-row gap-2 ">
                        <button
                          onClick={() =>
                            Navigate(`/productdetail/${product.slug}`)
                          }
                          className="btn btn-danger text-uppercase "
                          style={{ fontSize: "10px" }}
                        >
                          Details
                        </button>

                        <button
                          onClick={() => {
                            SetCart([...cart, product]);
                            localStorage.setItem(
                              "cart",
                              JSON.stringify([...cart, product])
                            );
                            toast.success(
                              ` ${product.productName}  added to the cart`
                            );
                          }}
                          className="btn btn-secondary text-uppercase   "
                        >
                          Add To Cart
                        </button>
                      </div>
                    </div>
                  </div>
                </>
              ))}
            </div>
            <div className=" py-2 px-5 d-flex flex-column justify-content-center">
              <p className="text-center text-muted fw-bold text-uppercase">
                {" "}
                total products : <span className="h4">{total}</span>
              </p>
              {products && products.length < total && (
                <button
                  className="btn btn-success py-3 fw-bold   h1"
                  onClick={(e) => {
                    e.preventDefault();
                    SetPage(page + 1);
                  }}
                >
                  {loading ? "loading" : "Load More"}
                </button>
              )}
            </div>
          </div>
        </div>
      </div>{" "}
    </>
  );
};

export default AllProducts;
